<template>
    <div class="warp">
        <div class="pathTitle">
            <p @click="handleHome"></p>
            <p v-for="item in pathTitleArr" :key="item.path" @click="handleTitleClick(item.path)">
                <i class="el-icon-arrow-right"></i>{{ item.pathName }} </p>
        </div>
        <div>
            <div class="detailsContent">
                <div class="title">{{ detailsData.title }}</div>
                <div class="title" style="font-size:12px">{{ detailsData.updateTime }}</div>
                <div v-html="detailsData.content" class="detailsText"></div>
            </div>
            <div style="text-align: center;padding-top: 20px;padding-bottom: 10px">
                <el-button style="background-color: #B83B13;border-color: #B83B13;"
                           type="primary"
                           @click="openUpdate">
                </el-button>
            </div>
        </div>

    <el-dialog title="" :visible.sync="perInfoAuth" width="30%">
      <el-tabs type="border-card" :value="tebIndex" @tab-click="tabFun">
        <el-tab-pane label="">
          <el-form
              :model="login"
              :rules="rules"
              ref="login"
              label-width="80px"
              class="demo-ruleForm"
          >
            <el-form-item label="" prop="name">
              <el-input v-model="login.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="pwd">
              <el-input type="password" v-model="login.pwd"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="">
          <el-form
              :model="register"
              :rules="rules"
              ref="register"
              label-width="80px"
              class="demo-ruleForm"
          >
            <el-form-item label="" prop="regname">
              <el-input v-model="register.regname"></el-input>
            </el-form-item>
            <el-form-item label="" prop="regpwd">
              <el-input
                  type="password"
                  v-model="register.regpwd"
                  autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="confirmPwd">
              <el-input
                  type="password"
                  v-model="register.confirmPwd"
                  autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="contactInfo">
              <el-input v-model="register.contactInfo"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="perInfoAuth = false"> </el-button>
        <el-button
            size="mini"
            v-if="tebIndex == 0"
            type="primary"
            @click="onSubmit('login')"
        > </el-button
        >
        <el-button
            size="mini"
            v-if="tebIndex == 1"
            type="primary"
            @click="onSubmit('register')"
        > </el-button
        >
      </span>
    </el-dialog>
    <el-dialog
        title=""
        :visible.sync="dialogUpload"
        width="30%"
        class="dialogUpload"
    >
      <el-upload
          drag
          :limit="limitNum"
          :auto-upload="false"
          accept=".png"
          :action="UploadUrl()"
          :before-upload="beforeUploadFile"
          :on-change="fileChange"
          :on-exceed="exceedFile"
          :on-success="handleSuccess"
          :on-error="handleError"
          :file-list="dataFileList"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">
          png50M
        </div>
      </el-upload>
      <!---->
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="uploadExit"> </el-button>
        <el-button size="mini" type="primary" @click="uploadSave"
        >
        </el-button
        >
      </div>
    </el-dialog>
    <!--      -->
    <el-dialog title="" :visible.sync="dialoginformation">
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <el-form
              :model="information"
              :rules="rules"
              ref="information"
              label-width="80px"
              class="demo-ruleForm"
          >
            <el-form-item label="" prop="Infoname">
              <el-input v-model="information.Infoname"></el-input>
            </el-form-item>
            <el-form-item label="" prop="gender">
              <el-select v-model="information.gender" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="certificates">
              <el-select
                  v-model="information.certificates"
                  placeholder=""
              >
                <el-option label="" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="Infoname">
              <el-input v-model="information.cardnum"></el-input>
            </el-form-item>
            <el-form-item label="" prop="Infoname">
              <el-input v-model="information.workadd"></el-input>
            </el-form-item>
            <el-form-item label="" prop="Infoname">
              <el-input v-model="information.worksite"></el-input>
            </el-form-item>
            <el-form-item
                label=""
                prop="email"
                :rules="[
                { required: true, message: '', trigger: 'blur' },
                {
                  type: 'email',
                  message: '',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input v-model="information.email"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      <!---->
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="infoExit"> </el-button>
        <el-button size="mini" type="primary" @click="infoSave('information')"
        >
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ItemHeader from "@/layout/ItemHeader";
import axios from "axios";
import PathTitle from "@/components/pathTitle.vue";

export default {
    name: "collectionDetails",
    components: {PathTitle, ItemHeader},
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else {
                if (this.register.regpwd !== "") {
                    this.$refs.register.validateField("checkPass");
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else if (value !== this.register.regpwd) {
                callback(new Error("!"));
            } else {
                callback();
            }
        };
        return {
            pathTitleArr: [
                {
                    path: '/jlhd', pathName: '',
                },
                {path: '/dazj', pathName: '', parentName: ''},
            ],
            publishType: "",
            title: "",
            publishContent: "",
            detailsData: {},
            perInfoAuth: false, //
            dialogUpload: false, //
            dialoginformation: false, //
            login: {
                name: "",
                pwd: "",
            },
            register: {
                regname: "",
                regpwd: "",
                confirmPwd: "",
                contactInfo: "",
            },
            information: {
                Infoname: "",
                gender: "",
                cardnum: "",
                workadd: "",
                worksite: "",
                email: "",
                certificates: "",
            },
            rules: {
                name: [{required: true, message: "", trigger: "blur"}],
                pwd: [{required: true, message: "", trigger: "blur"}],
                regname: [
                    {required: true, message: "", trigger: "blur"},
                ],
                regpwd: [{validator: validatePass, trigger: "blur"}],
                confirmPwd: [{validator: validatePass2, trigger: "blur"}],
                contactInfo: [
                    {required: true, message: "", trigger: "blur"},
                ],
                Infoname: [
                    {required: true, message: "", trigger: "blur"},
                ],
                gender: [{required: true, message: "", trigger: "change"}],
                certificates: [
                    {required: true, message: "", trigger: "change"},
                ],
                cardnum: [
                    {required: true, message: "", trigger: "blur"},
                ],
                workadd: [
                    {required: true, message: "", trigger: "blur"},
                ],
                worksite: [{required: true, message: "", trigger: "blur"}],
            },
            tebIndex: 0,
            limitNum: 1,
            //
            dataFileList: [],
        };
    },
    created() {
        axios({
            url: "/sysCollect/queryPagingCollectData",
            method: "POST",
            data: {id: this.$route.query.id, pageNum: 1, pageSize: 20},
        }).then(res => {
            if (res.data.code == 200) {
                this.detailsData = res.data.rows[0];
                console.log('this.detailsData ', res)
            }
        })
    },
    methods: {
        handleTitleClick(val) {
            this.$router.push({path: val});
        },
        openUpdate() {
            if (localStorage.getItem("token")) {
                axios({
                    url: "/appuser/getUserInfo",
                    method: "get",
                    headers: {
                        [`x-token`]: localStorage.getItem("token"),
                    },
                }).then((res) => {
                    console.log(res);
                    if (res.data.code == 200) {
                        console.log(res.data.data.status)
                        if (res.data.data.status == 0) {
                            this.dialoginformation = true; //
                        } else if (res.data.data.status == 2) {
                            this.$router.push({
                                path: 'personalcenter',
                                query: {detail: this.detailsData}
                            })
                        } else if (res.data.data.status == 1) {
                            this.$message.warning('')
                        } else {
                            this.$message.error('')
                            setTimeout(() => {
                                this.dialoginformation = true
                            }, 1000)
                        }
                    } else {
                        this.perInfoAuth = true
                    }
                });
            } else {
                this.perInfoAuth = true;
            }
        },
        tabFun(e) {
            this.tebIndex = e.index;
        },
        onSubmit(formName) {
            if (this.tebIndex == 0) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        axios({
                            url: "/appuser/login",
                            method: "POST",
                            params: {
                                userName: this.login.name,
                                passWord: this.login.pwd,
                            },
                        }).then((res) => {
                            if (res.data.code == 200) {
                                this.$message.success("");
                                this.perInfoAuth = false;
                                if (res.data.status == 0) {
                                    this.dialoginformation = true; //
                                } else if (res.data.status == 2) {
                                    this.$router.push({
                                        path: 'personalcenter',
                                        query: {detail: this.detailsData}
                                    })
                                } else if (res.data.status == 1) {
                                    this.$message.warning('')
                                } else {
                                    this.$message.error('')
                                    setTimeout(() => {
                                        this.dialoginformation = true
                                    }, 1000)
                                }
                                localStorage.setItem("token", res.data["x-token"]);
                            } else {
                                this.$message.error(res.data.data);
                            }
                        });
                    } else {
                        return false;
                    }
                });
            }
            // 
            if (this.tebIndex == 1) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        axios({
                            url: "/appuser/register",
                            method: "POST",
                            params: {
                                userName: this.register.regname,
                                passWord: this.register.regpwd,
                                phone: this.register.contactInfo,
                            },
                        }).then((res) => {
                            console.log(res);
                            if (res.data.code == 200) {
                                this.$message.success("");
                                this.tebIndex = "0";
                            } else {
                                this.$message.error(res.data.msg);
                            }
                        });
                    } else {
                        return false;
                    }
                });
            }
        },
        /**/
        UploadUrl() {
            // actionurlapi404action
            return "";
        },
        // 
        handleError(err, file, fileList) {
            this.$message.error("");
        },
        // 
        handleSuccess(res, file, fileList) {
            this.$message.success("");
        },
        // 
        exceedFile(files, fileList) {
            this.$message.warning(
                `${this.limitNum},`
            );
        },
        // 
        fileChange(file, fileList) {
            if (fileList.length > 0) {
                this.dataFileList = [fileList[fileList.length - 1]]
            }
        },
        beforeUploadFile(file) {
            let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
            let size = file.size / 1024 / 1024;
            //
            if (extension !== "png") {
                this.$message.warning("zip");
            }
            if (size > 50) {
                this.$message.warning("50M");
            }
        },
        infoSave(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    axios({
                        url: "/appuser/perfectInfo",
                        method: "POST",
                        headers: {
                            [`x-token`]: localStorage.getItem("token"),
                        },
                        params: {
                            realName: this.information.Infoname,
                            sex: this.information.gender,
                            cardType: this.information.certificates,
                            cardNum: this.information.cardnum,
                            workUnit: this.information.workadd,
                            address: this.information.worksite,
                            email: this.information.email,
                        },
                    }).then((res) => {
                        console.log(res);
                        if (res.data.code == 200) {
                            this.$message.success(res.data.msg);
                            console.log(res.data.msg);
                            this.dialoginformation = false;
                        } else {
                            // this.$message.warning(r);
                            this.dialoginformation = false;
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        infoExit() {
            this.dialoginformation = false;
        },
        /**/
        uploadExit() {
            this.dialogUpload = false;
            this.$message("");
        },
        /**/
        uploadSave() {
            if (this.dataFileList != '') {
                console.log(this.dataFileList)
                let form = new FormData();
                form.append('tid', this.detailsData.id);
                form.append('file', this.dataFileList[0].raw);
                let data = form
                axios({
                    url: "/sysCollect/uploadCollectData",
                    method: "POST",
                    headers: {
                        [`x-token`]: localStorage.getItem("token"),
                    },
                    data,
                }).then((res) => {
                    console.log(res);
                    if (res.data.code == 200) {
                        this.dialogUpload = false;
                        this.$message.success(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
                this.$message.error('');
            }
        },
        // 
        handleHome() {
            this.$router.push({path: '/index'});
        },
    },
    handleTitleClick(val) {
        this.$router.push({path: val});
        console.log('val', val)
    },
};
</script>

<style lang="scss" scoped>



.detailsContent {
  flex-grow: 1;
  width: 100%;
    .title {
        font-size: 30px;
        color: #5a3201;
        line-height: 40px;
        text-align: center;
        margin: 20px;
    }
    .title:nth-of-type(2){
        text-align: center;
        margin: 10px 0;
        font-size: 20px;
    }

    .title+div {
        p, .ql-align-justify {
            text-indent: 2em;
            margin-block-start: 1em;
            margin-block-end: 1em;
            font-size: 18px;
            color: #737373;
            line-height: 30px;

            br {
                display: none;
            }
            a{
                color: #B83B13;
                text-decoration: underline;
            }
        }
    }
  .dialogUpload {
    ::v-deep .el-upload-dragger {
      width: auto;
    }

  /deep/ .el-dialog__body {
    padding: 10px;

    & > div {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
  }
}
}

@media screen and (max-width: 600px){
  /deep/ .el-dialog{
    width: 90% !important;
  }
}
</style>
